.statistics {
	display: inline-block;
	width: 335px;
	margin: 15px;
	border: solid 1px rgb(211, 221, 221);
	background-color: #fff;
}

.title {
	font-size: 16px;
	color: #333333;
	text-align: center;
	margin-top: 30px;
	margin-bottom: 25px;
}

.statList {
	height: 60px;
	display: flex;
	list-style: none;
}

.item {
	width: 70px;
	border: solid 1px rgb(207, 221, 221);
}

.label {
	font-weight: 500;
	margin-top: 5px;
	margin-bottom: 5px;
	display: flex;
	justify-content: space-evenly;
}

.percentage {
	font-size: 16px;
	display: flex;
	justify-content: space-evenly;
}
